import React, { PureComponent } from 'react'
// import distanceInWords from 'date-fns/distance_in_words'
import Tabs from '../components/Tabs'
import { ENHANCED_TABS } from '../graphql/mutation/local'
import { ENHANCED_TABS as ENHANCED_TABS_QUERY } from '../graphql/query/local'
import PropertyPage from './Property'
import SitePage from './Site'
import UserPage from './User'

class Dashboard extends PureComponent {
  render() {
    const defaults = [
      {
        tab: 'User',
        container: <UserPage {...this.props} />,
      },
      {
        tab: 'Site',
        container: <SitePage {...this.props} />,
      },
      {
        tab: 'Property',
        container: <PropertyPage {...this.props} />,
      },
      // {
      //   tab: 'Stock',
      //   container: <StockPage {...this.props} />,
      // },
    ]
    return (
      <Tabs
        defaults={defaults}
        localStateMutation={ENHANCED_TABS}
        localStateQuery={ENHANCED_TABS_QUERY}
      />
    )
  }
}

export default Dashboard
