import Grid from '@material-ui/core/Grid'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import PersonAdd from '@material-ui/icons/PersonAdd'
import VpnKey from '@material-ui/icons/VpnKey'
import React from 'react'
import ChangePassword from '../components/Dialogs/ChangePassword'
import EnhancedMenu, { CreateMenuItem } from '../components/EnhancedMenu'
import Mutation from '../components/Mutation'
import { defaultState } from '../graphql/resolvers'
import { Routes } from '../Router'
import filter from '../utils/filter'

import { USER_ENTRY_TABLE as USER_ENTRY_TABLE_QUERY } from '../graphql/query/local'
import { USER_CREDIT_HISTORY, USERS } from '../graphql/query/remote'

import { USER_ENTRY_TABLE } from '../graphql/mutation/local'
import { DELETE_USERS } from '../graphql/mutation/remote'

import EnhancedTable from '../components/Enhanced/Table/Table'
import TableWrapper from '../components/TableWrapper'
import commanRender, { RENDER_VALUE } from '../utils/commanRender'
import getContactJSX from '../utils/getContactJSX'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    fab: {
      position: 'fixed',
      width: '4rem',
      bottom: theme.spacing.unit * 2,
      right: theme.spacing.unit * 2,
    },
  })

export const userColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created At',
  },
  {
    id: 'username',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Username',
  },
  {
    id: 'stockPermission',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Stock Permission',
  },
  {
    id: 'contact',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Phone Number',
  },
  {
    id: 'siteCount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Sites',
  },
  {
    id: 'totalSitesCost',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Sites Cost (₹)',
  },
  {
    id: 'totalReceivedAmount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Received Amount (₹)',
  },
  {
    id: 'spent',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Spent Amount (₹)',
  },
  {
    id: 'balance',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Balance Amount (₹)',
  },
]
export const userExclude = [
  'id',
  'count',
  'sites',
  'createdAt',
  'updatedAt',
  '__typename',
]

interface IUserProps extends WithStyles<typeof styles> {
  history?: any
}
const User = withStyles(styles)((props: IUserProps) => {
  const { classes, history, ...restProps } = props
  const { userEntryTable } = defaultState
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <TableWrapper
            tableMutation={USER_ENTRY_TABLE}
            tableQuery={USER_ENTRY_TABLE_QUERY}
            dataQuery={USERS}
            defaultState={userEntryTable}
          >
            {(
              tableSettings: typeof userEntryTable,
              lmutate: Function,
              { loading, data, refetch }: IGraphqlData,
            ) => {
              return (
                <Mutation mutation={DELETE_USERS}>
                  {(
                    deleteUsers: Function,
                    { loading: dloading }: IGraphqlData,
                  ) => {
                    return (
                      <EnhancedTable
                        {...restProps}
                        loading={loading || dloading}
                        data={data.users}
                        title='Users'
                        settings={tableSettings}
                        count={
                          (data.users.length && data.users[0].count) ||
                          data.users.length
                        }
                        columnData={userColumnData}
                        exclude={userExclude}
                        historyable={USER_CREDIT_HISTORY}
                        primaryButtonIcon={<PersonAdd />}
                        menu={(selected: any) => {
                          return selected.length === 1 ? (
                            <EnhancedMenu
                              options={[
                                (lprops: any) => {
                                  return (
                                    <ChangePassword
                                      {...lprops}
                                      reset={true}
                                      positiveHandler={(fn, args) => {
                                        return fn({
                                          variables: {
                                            id: selected[0],
                                            data: filter(args, [
                                              'confirmNewPassword',
                                            ]),
                                          },
                                        })
                                      }}
                                    >
                                      <CreateMenuItem
                                        {...lprops}
                                        icon={<VpnKey />}
                                        content='Reset Password'
                                        tooltip='Reset Password'
                                      />
                                    </ChangePassword>
                                  )
                                },
                              ]}
                            />
                          ) : null
                        }}
                        options={{
                          onCredit: async (fn: Function, args: any) => {
                            await fn({ variables: args })
                            // refetch();
                          },
                          onDelete: async (args: any) => {
                            await deleteUsers({ variables: args })
                            await lmutate({
                              variables: defaultState.userEntryTable,
                            })
                            refetch()
                          },
                          onAdd: () => history.push(Routes.User.create()),
                          onEdit: (e: any) =>
                            history.push(
                              Routes.User.update({ id: e, edit: true }),
                            ),
                        }}
                        render={commanRender({
                          match: 'spent',
                          renderValue: ({ key, value }: any) => {
                            if (key === 'contact') {
                              return { value: getContactJSX(value) }
                            }
                            return RENDER_VALUE({ key, value })
                          },
                        })}
                        mutate={async args => {
                          await lmutate({ variables: args })
                          // refetch();
                        }}
                      />
                    )
                  }}
                </Mutation>
              )
            }}
          </TableWrapper>
        </Grid>
      </Grid>
    </div>
  )
})

export default User
