import Grid from '@material-ui/core/Grid'
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from '@material-ui/core/styles'
import React from 'react'
import Mutation from '../components/Mutation'
import Query from '../components/Query'
import { defaultState } from '../graphql/resolvers'
import { Routes } from '../Router'
import formatDate from '../utils/date'

import { SITE_TABLE as SITE_TABLE_QUERY } from '../graphql/query/local'
import { SITES } from '../graphql/query/remote'

import { SITE_TABLE } from '../graphql/mutation/local'
import { DELETE_SITES } from '../graphql/mutation/remote'

import EnhancedTable from '../components/Enhanced/Table/Table'
import commanRender, { RENDER_VALUE } from '../utils/commanRender'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    fab: {
      position: 'fixed',
      width: '4rem',
      bottom: theme.spacing.unit * 2,
      right: theme.spacing.unit * 2,
    },
    lastEntryOn: {
      padding: '0 !important',
    },
  })

export const siteColumnData = [
  {
    id: 'createdAt',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Created At',
  },
  {
    id: 'name',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Name',
  },
  {
    id: 'location',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Location',
  },
  {
    id: 'entryCount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Entries',
  },
  {
    id: 'lastEntryDate',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Last Entry On',
  },
  {
    id: 'manager',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Manager',
  },
  {
    id: 'managerSpentAmount',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Manager Spent Amount (₹)',
  },
  {
    id: 'Total Cost',
    numeric: false,
    disablePadding: false,
    colSpan: 1,
    label: 'Total Cost (₹)',
  },
]
export const siteExclude = [
  'id',
  'count',
  'total',
  'createdAt',
  'updatedAt',
  '__typename',
]

interface ISiteProps extends WithStyles<typeof styles> {
  history?: any
}

const Site = withStyles(styles)((props: ISiteProps) => {
  const { classes, history, ...restProps } = props
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={SITE_TABLE}>
            {(lmutate: Function, args: IGraphqlData) => {
              return (
                <Query query={SITE_TABLE_QUERY}>
                  {({ data: { siteTable } }: IGraphqlData) => {
                    return (
                      <Mutation mutation={DELETE_SITES}>
                        {(deleteSites: Function) => {
                          const { page, rowsPerPage } = siteTable
                          return (
                            <Query
                              query={SITES}
                              variables={{
                                limit: rowsPerPage,
                                skip: page * rowsPerPage,
                              }}
                            >
                              {({ loading, data, refetch }: IGraphqlData) => {
                                // refetch();
                                return (
                                  <EnhancedTable
                                    {...restProps}
                                    loading={loading}
                                    data={data.sites}
                                    title='Sites'
                                    settings={siteTable}
                                    count={
                                      (data.sites.length &&
                                        data.sites[0].count) ||
                                      data.sites.length
                                    }
                                    columnData={siteColumnData}
                                    exclude={siteExclude}
                                    options={{
                                      onView: (e: any) =>
                                        history.push(
                                          Routes.Site.detail({ id: e }),
                                        ),
                                      // onEdit: (e: any) => history.push(Routes.Site.update({ id: e, edit: true })),
                                      onDelete: async (delArgs: any) => {
                                        await deleteSites({
                                          variables: delArgs,
                                        })
                                        await lmutate({
                                          variables: defaultState.siteTable,
                                        })
                                        refetch()
                                      },
                                      onAdd: () =>
                                        history.push(Routes.Site.create()),
                                    }}
                                    render={commanRender({
                                      match: 'managerSpentAmount',
                                      classes: (key: string) =>
                                        key === 'lastEntryDate'
                                          ? classes.lastEntryOn
                                          : '',
                                      renderValue: ({
                                        key,
                                        value,
                                        ...rest
                                      }: any) => {
                                        if (key === 'lastEntryDate') {
                                          return RENDER_VALUE({
                                            key,
                                            value: formatDate(value),
                                            ...rest,
                                          })
                                        }
                                        return RENDER_VALUE({
                                          key,
                                          value,
                                          ...rest,
                                        })
                                      },
                                    })}
                                    mutate={async largs => {
                                      await lmutate({ variables: largs })
                                      // refetch();
                                    }}
                                  />
                                )
                              }}
                            </Query>
                          )
                        }}
                      </Mutation>
                    )
                  }}
                </Query>
              )
            }}
          </Mutation>
        </Grid>
      </Grid>
    </div>
  )
})

export default Site
