const PropertyTableRenderer = (props: any) => {
  const {
    customRootClasses,
    title,
    loading: dloading,
    history,
    deleteProperties,
    wrapper,
    ...restProps
  } = props
  const {
    tableMutation,
    tableQuery,
    defaultState: ds,
    dataQuery,
    getQueryVariables,
  } = wrapper
  return (
    <TableWrapper
      tableMutation={tableMutation}
      tableQuery={tableQuery}
      defaultState={ds}
      dataQuery={dataQuery}
      getQueryVariables={getQueryVariables}
    >
      {(
        tableSettings: any,
        lmutate: Function,
        { loading, data, refetch }: IGraphqlData,
      ) => {
        const properties = data[Object.keys(data)[0]]
        return (
          <EnhancedTable
            {...restProps}
            loading={loading || dloading}
            data={properties}
            title={title}
            settings={tableSettings}
            count={
              (properties.length && properties[0].count) || properties.length
            }
            columnData={propertyColumnData}
            customRootClasses={customRootClasses}
            skipHistoryColumns={['type']}
            exclude={propertyExclude}
            historyable={PROPERTY_CREDIT_HISTORY}
            credit={
              <PropertyCreditDialog
                {...restProps}
                positiveHandler={(m, e, selected) =>
                  m({ variables: { ...e, id: selected } })
                }
              />
            }
            primaryButtonIcon={<HomeIcon />}
            // menu={<EnhancedMenu/>}
            options={{
              onEdit: (e: any) =>
                history.push(
                  Routes.Property.update({
                    id: e,
                    edit: true,
                  }),
                ),
              onCredit: async (fn: Function, args: any) => {
                await fn({ variables: args })
                // refetch();
              },
              onDelete: async (args: any) => {
                await deleteProperties({ variables: args })
                await lmutate({
                  variables: defaultState,
                })
                refetch()
              },
              onAdd: () => history.push(Routes.Property.create()),
            }}
            // render={(key: string, items: any, colorValue: string) => {
            //   console.log(items)
            //   if (key.includes('price')) {
            //     const value = items
            //     const bgcolor = value ? BACKGROUND_COLOR : undefined
            //     return (
            //       <TableCell
            //         style={{
            //           // color: val.color || colorValue,
            //           backgroundColor: bgcolor,
            //         }}
            //       >
            //         {value}
            //       </TableCell>
            //     )
            //   }
            //   return (
            //     <TableCell
            //       style={{
            //         color: colorValue,
            //       }}
            //     >
            //       {items}
            //     </TableCell>
            //   )
            // }}
            render={commanRender({
              match: 'totalReceivedAmount',
              renderValue: ({ key, value }: { key: string; value: any }) => {
                if (key === 'nextDueDate') {
                  return { value: formatDate(value) }
                } else if (key === 'buyerNumber') {
                  return { value: getContactJSX(value) }
                }
                const result = RENDER_VALUE({
                  key,
                  value,
                  skip: ['buyerNumber'],
                })
                return {
                  ...result,
                  value: String(value).length ? result.value : '-',
                }
              },
            })}
            mutate={async args => {
              await lmutate({ variables: args })
              // refetch();
            }}
          />
        )
      }}
    </TableWrapper>
  )
}

interface IProperty extends WithStyles<typeof styles> {
  history?: any
}
const Property = withStyles(styles)((props: IProperty) => {
  const { classes, history, ...restProps } = props
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={DELETE_PROPERTIES}>
            {(
              deleteProperties: Function,
              { loading: dloading }: IGraphqlData,
            ) => {
              return (
                <React.Fragment>
                  <Query query={PROPERTY_CREDIT_HISTORY_TOTAL}>
                    {({ loading, data: { propertyTotal } }: IGraphqlData) => {
                      return (
                        <Paper
                          style={{
                            padding: '2rem',
                            margin: '1rem 0rem',
                          }}
                        >
                          <Grid container spacing={16} justify='space-between'>
                            <Grid item>
                              <Typography variant='h5' color='primary'>
                                Total Price (₹):{' '}
                                {toCurrency(propertyTotal.price)}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography variant='h5'>
                                Total Received Amount (₹):{' '}
                                {toCurrency(propertyTotal.totalReceivedAmount)}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography
                                variant='h5'
                                color={
                                  propertyTotal.balance < 0
                                    ? 'primary'
                                    : 'secondary'
                                }
                              >
                                Total Balance (₹):{' '}
                                {toCurrency(propertyTotal.balance)}
                              </Typography>
                            </Grid>
                          </Grid>
                        </Paper>
                      )
                    }}
                  </Query>
                  <PropertyTableRenderer
                    {...restProps}
                    customRootClasses={[classes.duePropertyRoot]}
                    title='Due Properties'
                    loading={dloading}
                    history={history}
                    deleteProperties={deleteProperties}
                    wrapper={{
                      tableMutation: DUE_PROPERTY_TABLE,
                      tableQuery: DUE_PROPERTY_TABLE_QUERY,
                      defaultState: defaultState.duePropertyTable,
                      dataQuery: DUE_PROPERTIES,
                      getQueryVariables: (_: any) => ({}),
                    }}
                  />
                  <PropertyTableRenderer
                    {...restProps}
                    title='Properties'
                    loading={dloading}
                    history={history}
                    deleteProperties={deleteProperties}
                    wrapper={{
                      tableMutation: PROPERTY_TABLE,
                      tableQuery: PROPERTY_TABLE_QUERY,
                      defaultState: defaultState.propertyTable,
                      dataQuery: PROPERTIES,
                      getQueryVariables: (_: any) => ({}),
                    }}
                  />
                </React.Fragment>
              )
            }}
          </Mutation>
        </Grid>
      </Grid>
    </div>
  )
})

export default Property
